@import "./variables";

body, html {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  background-color: $background-color;
  color: $text-color;
  font-family: "Poppins", sans-serif;
}

.section {
  padding: 100px 10%;
  text-align: center;

  h2 {
    color: $primary-color;
    margin-bottom: 20px;
  }

  p {
    max-width: 700px;
    margin: 0 auto 40px auto;
    line-height: 1.6;
  }
}

button {
  background-color: $primary-color;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  transition: 0.3s;

  &.secondary {
    background-color: $accent-color;
    margin-left: 10px;
  }

  &:hover {
    opacity: 0.85;
  }
}
#projects{
  h2{
    margin-top: -40px;
  }
}
.project-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
   a{
      text-decoration: none;
      color: inherit;
      
    }

  .project-card {
    background: white;
    border-radius: 12px;
    padding: 20px;
    max-width: 320px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
    transition: 0.3s;
    height: 80%;
    display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;


     #biocode{
      background: rgb(12, 86, 170);
    height: 140px; /* igual às outras imagens */
    object-fit: contain;    }
    

    &:hover {
      transform: translateY(-4px);
    }

    img {
      width: 100%;
    height: 160px; /* padroniza as imagens */
    object-fit: cover; /* corta proporcionalmente */
    border-radius: 10px;
    margin-bottom: 15px;
    }
     

    h3 {
        color: $primary-color;
    font-weight: 600;
    font-size: 1.1rem;
    margin: 10px 0;
    min-height: 3rem; /* deixa todos os títulos alinhados */
    display: flex;
    align-items: center;
    justify-content: center;
    }
  }
}
/* --- MODAL --- */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.65);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-box {
 background: white;
  width: 90%;
  max-width: 450px;      /* menor e elegante */
  max-height: 80vh;      /* não estoura a tela */
  overflow-y: auto;      /* cria scroll interno */
  padding: 25px;
  border-radius: 16px;   /* bordas lindas */
  position: relative;
  animation: fadeIn 0.25s ease;
  text-align: left;

  box-shadow: 0 8px 25px rgba(0,0,0,0.25); /* sombra mais clean */
}

.close-btn {
   position: absolute;
  top: 12px;
  right: 12px;
  background: #ff4d4d;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 4px 9px;
  font-size: 0.9rem;
  cursor: pointer;
}

.modal-main-img {
  width: 100%;
  border-radius: 12px;
  margin: 15px 0;
}

.modal-link {
  display: inline-block;
  margin-top: 15px;
  background: $primary-color;
  color: white;
  padding: 10px 15px;
  border-radius: 8px;
  text-decoration: none;
}

.extra-images {
   margin-top: 15px;
  display: flex;
  gap: 10px;

  img {
    width: 30%;
    border-radius: 8px;
  }
}

@keyframes fadeIn {
  from { opacity: 0; transform: scale(0.95); }
  to   { opacity: 1; transform: scale(1); }
}

.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;

  span {
    background: $accent-color;
    color: white;
    padding: 6px 12px;
    border-radius: 20px;
    font-size: 0.9rem;
  }
}

.contact-links a {
  display: block;
  margin: 8px 0;
  color: $primary-color;
  text-decoration: none;
  font-weight: 500;

  &:hover {
    text-decoration: wavy;
  }
}
.divisor {
  border-bottom: 2px solid #00aeff;  // linha azul
  width: 60%;
  margin: 40px auto;                 // centralizada
}
